.featured{
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.featuredItem{
    flex: 1;
    margin: 0px 20px;
    padding: 30px;
    border-radius: 10px;
    cursor: pointer;
    -webkit-box-shadow: 1px 1px 5px 1px rgba(0,0,0,0.86);
box-shadow: 1px 1px 5px 1px rgba(0,0,0,0.86);
}

.featuredTitle{
    font-size: 20px;
}
.batimentTotal{
    margin: 10px 0px;
    display: flex;
    align-items: center;
}

.nombrebatiment{
    font-size: 18px;
    font-weight: 400;
    color: #304CA6;
    align-items: center;
    margin-left: 20px;
}