.sidebar{
    flex: 1;
    height: calc(100vh - 50px );
    background-color: rgb(255, 255, 255);
    position: sticky;
    top: 50px;
    text-decoration: none;
}

.sidebareWrapper{
    color: rgb(0, 0, 0);
    cursor: pointer;
    padding: 20px;
}

.sidebarMenu{
    padding: 10px;
}

.sidebarList{
    text-decoration: none;
    padding: 10px;
}

.sidebareTitle{
    color: #304CA6;
    font-size: 15px;
}

.sidebarList{
    list-style: none;
    padding: 5px;
    text-decoration: none !important;
}
.sidebarItem{
    padding: 5px;
    cursor: pointer;
    display: flex;
    align-items: center;
    border-radius: 8px;
}

.sidebarItem.active,
.sidebarItem:hover{
    background-color: rgb(243, 242, 242);
    color: #304CA6;
    text-decoration: none;
}

.sidebarIcon{
    margin-right: 5px;
    font-size: 20px !important;
}

.lien{
    text-decoration: none;
    color: inherit;
}