.topbar{
    width: 100%;
    height: 50px;
    background-color: white;
    position: sticky;
    top: 0;
    z-index: 999;
    -webkit-box-shadow: 0px 3px 4px 0px rgba(0,0,0,0.81);
box-shadow: 0px 3px 4px 0px rgba(0,0,0,0.81);
}

.topbarWrapper{
    height: 100%;
    padding: 0px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.logo{
    font-weight: bold;
    font-size: 30px;
    color: #304CA6;
    cursor: pointer;
}
.topRight{
    display: flex;
    align-items: center;
}



.lienLogo{
    color: inherit;
    text-decoration: none;
}
.topbarIcon{
    position: relative;
    cursor: pointer;
    margin-right: 30px;
    color: #304CA6;
}
.topbarIcon>button{
    color: #304CA6;
    border: none;
    background: none;
}
